.header {
    height: 3.5rem;
    width: 100%;
    background-color: var(--light-color1);
    display: flex;
    justify-content: space-between;
    align-items: center;
    color:var(--dark-color3);
}

.logo-header {
    margin-left: 2rem;
}

.logo-img {
    height: 2rem;
    width: 15rem;
    background-image: url('../../images/logo.png');
    background-repeat: no-repeat;
    background-size: cover;

}

.nav-container {
    height: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 2rem;
}

.nav {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100%;
    width: 8rem;
    cursor: pointer;
}
.nav:hover {
    background-color: var(--light-color2);
    transition: background-color 1s;
    
}