.card-container {
    margin: 1rem 1rem 0; 
    height: 32rem;
    width: 17rem;
    overflow: auto;
}

.card-container::-webkit-scrollbar {
    background-color: var(--light-color1);
    border-radius: 20px;
    width: .5rem;
}

.card-container::-webkit-scrollbar-thumb {
    background-color: var(--light-color2);
    border-radius: 20px;
    
}

@keyframes FadeCard {
    from {transform: scale(0);opacity: 0;}
    to {transform: scale(100%);opacity: 1;}
  }
  
.card {
    min-height: 8rem;
    width: 15rem;
    margin-bottom: 1rem; 
    background-color: var(--light-color1);
    padding: .8rem;
    user-select: none;
    animation-name: FadeCard;
    animation-duration: .5s;
    word-break: break-all;
}

.card-info {
    display: flex;
    align-items: center;
    margin-bottom: .4rem;
    color: var(--dark-color1);
}

.card-ico {
    min-height: 1.2rem;
    min-width: 1.2rem;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center;
    margin-right: .2rem;
}

.pet {
    background-image: url('../../images/icons/pet.png');
}
.user {
    background-image: url('../../images/icons/user.png');
}
.date {
    background-image: url('../../images/icons/date.png');
}
.symp {
    background-image: url('../../images/icons/symp.png');
}

.btn-delete {
    border: 1px solid red;
    height: 1.5rem;
    width: 5rem;
    outline: none;
    background-color: var(--dark-color1);
    color: #fff;
    border: none;
    font-family: 'Montserrat-m';
    cursor: pointer;
}

.btn-delete:active {
    transform: scale(.95);
}