.form {
    height: 28rem;
    width: 20rem;
    margin-top: 1rem;
    flex: 2rem 5rem;
    background-color: var(--light-color1);
    color: var(--dark-color1);
}

.form-header {
    height: 3rem;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    font-family: 'Montserrat-l';
    font-size: 1.2rem;
}

.form-group {
    display: flex;
    flex-direction: column;
}

.form-input {
    padding: 1rem;
    display: flex;
    flex-direction: column;
}

.form-input input {
    border: none;
    height: 1.5rem;
    border-radius: 2px;
    outline-color: var(--light-color2);
    color: var(--dark-color3);
}

.form-input textarea {
    border: none;
    resize: none;
    height: 4rem;
    border-radius: 2px;
    outline-color: var(--light-color2);
    color: var(--dark-color3);
    font-family: 'Montserrat-m';
}

.add-btn {
    height: 1.5rem;
    outline: none;
    background-color: var(--dark-color1);
    color: #fff;
    border: none;
    font-family: 'Montserrat-m';
    cursor: pointer;
}

.add-btn:active {
    transform: scale(.95);
}

@keyframes modalFade {
    from {transform: translateY(-50%);opacity: 0;}
    to {transform: translateY(0);opacity: 1;}
  }
  
.PopUp{
    height: 2rem;
    color: #fff;
    font-family: 'Montserrat-l';
    display: flex;
    justify-content: center;
    align-items: center;
    animation-name: modalFade;
    animation-duration: .5s;
    user-select: none;
}

.error {
    background-color: #e24e4e;
}
.success {
    background-color: #6db35f;
}

.delete {
    background-color: #e2c051;
}
  